@import './scss/style.scss';

.App{

  box-sizing: border-box;
}
body{
  &::-webkit-scrollbar {
    width: 1.2rem;
    background-color: #f5f5f5;
  }
  &::-webkit-scrollbar-thumb
  {
    border-radius: 10px;
    background-color: #555;
  }

}
.padding{
    padding: 30rem 0;
}
.product{
  margin: 5rem 0
}

.Toastify__toast-body{
  font-size: 1.4rem;
}


// custom Input Field

.login-form{
  .form__group{
    width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 300px;
        height: 47px;
        border: 1px solid #bebebe;
        border-radius: 5px;
        &--button{
          border: none;
        }
  }
  .form__field{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 30rem;
    height: 4.7rem;
    // border: 1px solid #bebebe;
    border-radius: 5px;
    margin-bottom: 3rem;
  }
  .form__error{
    color: #f3327c;
      font-size: 1.2rem;
      margin: .5rem ;
  }
  img{
      margin: 1rem;
      width: 2rem;
      height: auto;
      opacity: .5;
  }
  input{
    width: 100%;
    margin-right: .5rem;
    font-size: 1.6rem;
    border: none;
    outline: none;
    height: 100%;
    padding-left: .5rem;
    &:-webkit-autofill,
    &:-webkit-autofill:hover,
    &:-webkit-autofill:focus{
      border: 1px solid transparent;
      -webkit-text-fill-color: #222;
      -webkit-box-shadow: 0 0 0px 1000px #0000 inset;
      transition: background-color 5000s ease-in-out 0s;
      font-size: 1.4rem;
    }
  }
}
.checkout-form{
  width: 100%;
  .form__field{
    .form__group{
      margin: 1rem 0rem;
      input{
        width: 100%;
        padding: 1rem ;
        box-sizing: border-box;
        font-size: 1.3rem;
      }
      select{
        width: 100%;
        padding: 1rem;
        box-sizing: border-box;
        font-size: 1.3rem;
        option{
          font-size: 1.3rem;
        }
      }
    }
    .form__error{
      color: red;
      font-size: 1.4rem;
    }
  }

}



